import { useState, useEffect } from 'react';
import { useParams } from 'react-router-dom';
import ErrorMessage from '../components/error-message/error-message';
import axios from 'axios';
import avatar from './photo.jpg';
import './student.scss';

const Student = () => {

    const [firstName, setFirstName] = useState('');
    const [lastName, setLastName] = useState('');
    const [patronymic, setPatronymic] = useState('');
    const [age, setAge] = useState('');
    const [position, setPosition] = useState('');
    const [stack, setStack] = useState([]);
    const [day, setDay] = useState([]);
    const [error, setError] = useState([]);
    const [isLoaded, setIsLoaded] = useState(false);

    const { id } = useParams();

    useEffect(() => {
        const options = {
            method: 'GET',
            url: `https://javaforall.tech/api/front/developer/${id}`,
            mode: 'cors',
            headers: {
                "Content-Type": "application/json",
                'Access-Control-Allow-Origin': '*',
            },
        };

        axios
            .request(options)
            .then(function (response) {
                console.log(response);
                setFirstName(response.firstName);
                setLastName(response.lastName);
                setPatronymic(response.patronymic);
                setAge(response.age);
                setPosition(response.position);
                setStack(response.stack);
                setDay(response.daysWorkList);
                setIsLoaded(true);
            })
            .catch(function (error) {
                console.error(error);
                if (error.response.data !== undefined) {
                    setError(error.response.data);
                    setIsLoaded(false);
                } else {
                    setError(error);
                    setIsLoaded(false);
                }
            });
    }, [id]);

    return (
        <div className="profile">
            <div className="profile__img-wrapper">
                <img src={avatar} alt="" />
            </div>
            <div className="profile__info">
                <h3 className="profile"></h3>
                <div className="profile__item">
                    <p id='name'>:</p>
                    <label htmlFor="name">{lastName}</label>
                </div>
                <div className="profile__item">
                    <p id='name'>:</p>
                    <label htmlFor="name">{firstName}</label>
                </div>
                <div className="profile__item">
                    <p id='name'>:</p>
                    <label htmlFor="name">{patronymic}</label>
                </div>
                <div className="profile__item">
                    <p id='name'>:</p>
                    <label htmlFor="name">{age}</label>
                </div>
                <div className="profile__item">
                    <p id='name'>:</p>
                    <label htmlFor="name">{position}</label>
                </div>
                <div className="profile__item">
                    <p id='name'>:</p>
                    <label htmlFor="name">{stack.toString()}</label>
                </div>
                <div className="profile__item">
                    <p id='name'>:</p>
                    <label htmlFor="name">{day.toString()}</label>
                </div>
            </div>
            <div className="profile__error">
                {isLoaded ? null : <ErrorMessage error={error} />}
            </div>
        </div>
    )
}

export default Student