import React from 'react';
import "./app-filter.scss";
import { DropdownButton } from 'react-bootstrap';
import { Dropdown } from 'react-bootstrap';

const AppFilter = (props) => {

    return (
        <div className='dropdown-wrapper'>
            <DropdownButton id="dropdown-basic-button" title=":">
                <Dropdown.Item onClick={() => props.onFilterSelect('all')} href="#/action-1"></Dropdown.Item>
                <Dropdown.Item onClick={() => props.onFilterSelect('MONDAY')} href="#/action-2"></Dropdown.Item>
                <Dropdown.Item onClick={() => props.onFilterSelect('TUETHDAY')} href="#/action-3"></Dropdown.Item>
                <Dropdown.Item onClick={() => props.onFilterSelect('WEDNESDAY')} href="#/action-4"></Dropdown.Item>
                <Dropdown.Item onClick={() => props.onFilterSelect('THURSDAY')} href="#/action-5"></Dropdown.Item>
                <Dropdown.Item onClick={() => props.onFilterSelect('FRIDAY')} href="#/action-6"></Dropdown.Item>
                <Dropdown.Item onClick={() => props.onFilterSelect('SATURDAY')} href="#/action-7"></Dropdown.Item>
                <Dropdown.Item onClick={() => props.onFilterSelect('SUNDAY')} href="#/action-8"></Dropdown.Item>
            </DropdownButton>
        </div>
    )
}

export default AppFilter;