import { useState } from "react";
import { Button, Form, Spinner } from 'react-bootstrap';
import { useNavigate } from 'react-router-dom';
import qs from 'qs';
import axios from 'axios';
import './auth.scss';
import ErrorMessage from '../../components/error-message/error-message';

const Auth = (props) => {
    const [email, setEmail] = useState('');
    const [password, setPassword] = useState('');
    const [error, setError] = useState(false);
    const [errorMessage, setErrorMessage] = useState('');
    const [isLoading, setIsLoading] = useState(false);


    const history = useNavigate();

    const onChange = (e) => {
        if (e.target.name === 'email') {
            setEmail(e.target.value)
        } else if (e.target.name === 'password') {
            setPassword(e.target.value);
        }
    }

    const onSubmit = (e) => {
        e.preventDefault();
        setIsLoading(true);
        const data = {
            'grant_type': 'password',
            'client_id': 'template-service',
            'client_secret': '94071761-d448-4c0b-bdbd-3a53b7e0af2e',
            'username': email,
            'password': password
        }

        const options = {
            method: 'post',
            url: 'https://javaforall.tech/oauth/auth/realms/template/protocol/openid-connect/token',
            headers: { 'content-type': 'application/x-www-form-urlencoded' },
            data: qs.stringify(data),
        }
        axios(options)
            .then(function (response) {
                console.log(response);
                setError(false);
                history('/');
                props.setIsAuth();
                setIsLoading(false)
            })
            .catch(function (error) {
                console.error(error);
                setError(true);
                setIsLoading(false);
                if (error.response.data !== undefined) {
                    setErrorMessage(error.response.data);
                } else {
                    setErrorMessage(error);
                }
            });
    }

    return (
        <div className="auth">
            <div className="auth__title">
                <h1></h1>
            </div>
            <div className="auth__form">
                {isLoading ?
                    <div className="spinner__wrapper">
                        <Spinner animation="grow" variant="primary" />
                        <Spinner animation="grow" variant="secondary" />
                        <Spinner animation="grow" variant="success" />
                        <Spinner animation="grow" variant="danger" />
                        <Spinner animation="grow" variant="warning" />
                        <Spinner animation="grow" variant="info" />
                        <Spinner animation="grow" variant="light" />
                    </div>
                    :
                    <Form className="form" onSubmit={onSubmit}>
                        <Form.Group className="mb-3" controlId="formBasicEmail">
                            <Form.Control required className="auth__input" onChange={onChange} name='email' value={email} type="text" placeholder=" email" />
                        </Form.Group>

                        <Form.Group className="mb-3" controlId="formBasicPassword">
                            <Form.Control required className="auth__input" onChange={onChange} name='password' value={password} type="password" placeholder=" " />
                        </Form.Group>
                        <div className="auth__button">
                            <Button variant="primary" type="submit">
                                
                            </Button>
                        </div>
                    </Form>
                }
            </div>
            <div className="error">
                {error ? <ErrorMessage error={errorMessage} /> : null}
            </div>
        </div>
    )
}

export default Auth;