import { useState, useEffect } from 'react';
import { Routes, Route } from 'react-router-dom'
import { nanoid } from 'nanoid';
import NavBar from '../navbar/navbar';
import JavaForallSevices from '../../services/javaForalServices';
import Student from '../../pages/student';
import Home from '../../pages/home';
import ErrorMessage from '../error-message/error-message';
import Auth from '../../pages/auth/auth';
import axios from 'axios';
import './app.css';

const App = (props) => {

    const [users, setUsers] = useState([]);
    const [term, setTerm] = useState('');
    const [filter, setFilter] = useState('all');
    const [isError, setIsError] = useState(false);
    const [error, setError] = useState('');
    const [isAuth, setIsAuth] = useState(false);

    useEffect(() => {
        const javaForalServices = new JavaForallSevices();

        javaForalServices
            .getAllUsers()
            .then((response) => {
                console.log(response);
                const elements = response.data;
                setUsers([...elements]);
            })
            .catch((error) => {
                console.error(error);
                if (error.response.data !== undefined) {
                    setIsError(true);
                    setError(error.response.data);
                } else {
                    setIsError(true);
                    setError(error);
                }
            });
    }, []);

    const deleteItem = (id) => {
        setUsers(users.filter(item => item.id !== id));

        const options = {
            method: 'DELETE',
            url: `https://javaforall.tech/api/front/developer/${id}`,
            mode: 'cors',
            headers: {
                'Access-Control-Allow-Origin': '*',
            },
        };

        axios
            .request(options)
            .then((response) => {
                console.log(response);
            })
            .catch((error) => {
                console.error(error);
                if (error.response.data !== undefined) {
                    setIsError(true);
                    setError(error.response.data);
                } else {
                    setIsError(true);
                    setError(error);
                }
            });
    }

    const addItem = (firstName, lastName, patronymic, age, daysWorkList, experience, position, stack) => {
        const randomId = nanoid()
        const newItem = {
            age,
            daysWorkList,
            experience,
            firstName,
            lastName,
            patronymic,
            position,
            stack,
            id: randomId,
        }
        const body = {
            age,
            daysWorkList,
            experience,
            firstName,
            lastName,
            patronymic,
            position,
            stack,
        }

        const options = {
            method: 'POST',
            url: `https://javaforall.tech/api/front/developer`,
            mode: 'cors',
            headers: {
                'Content-Type': 'application/json;charset=utf-8',
                'Access-Control-Allow-Origin': '*',
            },
            body: JSON.stringify(body),
        };

        axios
            .request(options)
            .then(function (response) {
                console.log(response);
            })
            .catch((error) => {
                console.error(error);
                if (error.response.data !== undefined) {
                    setIsError(true);
                    setError(error.response.data);
                } else {
                    setIsError(true);
                    setError(error);
                }
            });

        const newArr = [...users, newItem];
        setUsers(newArr);
    }

    const searchEmp = (items, term) => {
        if (term.length === 0) {
            return items;
        }

        return items.filter(item => {
            return item.firstName.indexOf(term) > -1
        })
    }

    const onUpdateSearch = (term) => {
        setTerm(term);
    }

    const filterPost = (items, filter) => {
        switch (filter) {
            case 'MONDAY':
                return items.filter(item => item.daysWorkList.includes('MONDAY'));
            case 'TUETHDAY':
                return items.filter(item => item.daysWorkList.includes('TUETHDAY'));
            case 'WEDNESDAY':
                return items.filter(item => item.daysWorkList.includes('WEDNESDAY'));
            case 'THURSDAY':
                return items.filter(item => item.daysWorkList.includes('THURSDAY'));
            case 'FRIDAY':
                return items.filter(item => item.daysWorkList.includes('FRIDAY'));
            case 'SATURDAY':
                return items.filter(item => item.daysWorkList.includes('SATURDAY'));
            case 'SUNDAY':
                return items.filter(item => item.daysWorkList.includes('SUNDAY'));
            default:
                return items
        }
    }

    const onFilterSelect = (filter) => {
        setFilter(filter);
    }

    const setAuth = () => {
        setIsAuth(true);
    }
    const setAuthOutput = () => {
        setIsAuth(false);
    }

    const qunUsers = users.length;
    const visibleData = filterPost(searchEmp(users, term), filter);

    return (
        <div className="app">
            <NavBar isAuth={isAuth} setIsAuth={() => setAuthOutput()} />
            <Routes>
                <Route path="/" element={
                    <Home
                        onUpdateSearch={onUpdateSearch}
                        filter={filter}
                        onFilterSelect={onFilterSelect}
                        users={qunUsers}
                        data={visibleData}
                        onDelete={deleteItem}
                        onAdd={addItem}
                        isAuth={isAuth}
                    />
                } />
                <Route path="/student/:id" element={
                    isAuth ? <Student /> : <Auth setIsAuth={() => { setAuth() }} />
                } />
                <Route path="/auth" element={
                    <Auth setIsAuth={() => { setAuth() }} />
                } />
            </Routes>
            <div className="error">
                {isError ? <ErrorMessage error={error} /> : null}
            </div>
        </div>
    );
}

export default App;